import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm } from "redux-form";
import { getParaSystem } from "../../../actions/utility_action";
import { ToastNotification } from "../../../components/helpers/notification";
import { Panel, PanelHeader } from "../../../components/panel/panel";
import { postData2 } from "../../../config/axios";
import FormParameterPoint from "./FormParameterPoint";

const maptostate = (state) => {
  return {
    DataSystem: state.utility.getParaSystem,
  };
};
class SettingSystem extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);

    this.state = {
      activeTab: "1",
      isLoading : false
    };
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  }
  componentDidMount() {
    window.history.pushState(null, document.title, window.location.href);
    window.addEventListener("popstate", function (event) {
      window.history.pushState(null, document.title, window.location.href);
    });
    this.props.dispatch(getParaSystem());
  }
  // setSystem(data) {
  //   let hasil = {
  //     key: "kategori_harga",
  //     value: data.target.value,
  //     type: "kategori_harga",
  //   };
  //   postData2("para-system/", hasil).then(() => {
  //     ToastNotification("success", "Katgeori Harga Berhasil Diubah");
  //   });
  // }
  // setTypePoin(data) {
  //   let hasil = {
  //     key: "TYPE_POIN",
  //     value: data.target.value,
  //     type: "TYPE_POIN",
  //   };

  //   postData2("para-system/", hasil).then(() => {
  //     ToastNotification("success", "Type Poin Berhasil Diubah");
  //   });
  // }

  handleSubmit(data) {
    // console.log(data);
    let kategori_harga = {
      key: "KATEGORI_HARGA",
      value: data.kategori_harga,
      type: "kategori_harga",
    };
    let type_poin = {
      key: "TIPE_POIN",
      value: data.type_poin,
      type: "type_poin",
    };
    let cashback = {
      key: "CASHBACK_POIN",
      value: data.cashback_poin,
      type: "cashback_poin",
    };
    let harga_max = {
      key: "HARGA_MAX",
      value: data.harga_max,
      type: "harga_max",
    };

    let hasil = [type_poin, kategori_harga,cashback,harga_max];
    // console.log(hasil);
    this.setState({
      isLoading : true
    })
    postData2("para-system", hasil)
      .then((res) => {
        ToastNotification("success", "Setting System Berhasil Diubah");
        this.props.dispatch(getParaSystem());
        this.setState({
          isLoading : false
        })
      })
      .catch((err) => {
        // console.log(err);
        this.setState({
          isLoading : false
        })
      });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Dashboard</Link>
          </li>
          <li className="breadcrumb-item">Setting System</li>
        </ol>
        <h1 className="page-header">Setting System</h1>
        <Panel>
          <PanelHeader>Setting System</PanelHeader>
          <br />
          {/* Blank Page */}
          <div className="container">
            <FormParameterPoint
              isLoading={this.state.isLoading}
              DataSystem={this.props.DataSystem}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </div>
          <br />
        </Panel>
        {/* End Tambah Blank Page  */}
      </div>
    );
  }
}

// export default SettingSystem;
SettingSystem = reduxForm({
  form: "SettingSystem",
  enableReinitialize: true,
})(SettingSystem);
export default connect(maptostate, null)(SettingSystem);
