import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: " ,-",
  locale: "kr-KO",
});
const kategori_harga = [
  {
    value: "harga_100%",
    name: "HARGA 100%",
  },
  {
    value: "harga_per_gram",
    name: "HARGA / GRAM",
  },
];
const type_poin = [
  {
    value: "GRAM",
    name: "GRAM",
  },
  {
    value: "RP",
    name: "RP",
  },
];
class FormParameterPoint extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showMember: true,
    };
  }
  componentDidMount() {
    // this.props.dispatch(getParaSystem());
  }

  render() {
    setTimeout(() => {
      this.props.DataSystem.forEach((element) => {
        // console.log(element);
        if (element.key === "KATEGORI_HARGA") {
          this.props.change("kategori_harga", element.value);
        }
        if (element.key === "TIPE_POIN") {
          this.props.change("type_poin", element.value);
        }
        if (element.key === "CASHBACK_POIN") {
          this.props.change("cashback_poin", element.value);
        }
        if (element.key === "HARGA_MAX") {
          this.props.change("harga_max", element.value);
        }
      });
    }, 1000);
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <Field
              name="kategori_harga"
              label="Pilih Kategori Harga"
              placeholder="Masukan Kategori Harga"
              options={kategori_harga}
              component={ReanderSelect}
            />
          </div>
          <div className="col-4">
            <Field
              name="type_poin"
              label="Pilih Type Poin"
              placeholder="Masukan Type Poin"
              options={type_poin}
              component={ReanderSelect}
            />
          </div>
          <div className="col-4">
            <Field
              name="cashback_poin"
              label="Cashback Poin"
              placeholder="Masukan Cashback Poin"
              text="text"
              {...currencyMask}
              component={ReanderField}
            />
          </div>
          <div className="col-4">
            <Field
              name="harga_max"
              label="Harga Max"
              placeholder="Masukan Harga Max"
              text="text"
              {...currencyMask}
              component={ReanderField}
            />
          </div>
          <div className="col-4">
            <label> &nbsp;</label>
            <button
              className="btn btn-primary btn-block"
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormParameterPoint = reduxForm({
  form: "FormParameterPoint",
  enableReinitialize: true,
})(FormParameterPoint);
export default connect()(FormParameterPoint);
